import {
	LOGIN_BG,AVATAR
} from '@/config/image.js';
import {sendVerificationCode,bindPhone} from '@/api/mine.js';
import {toast} from '@/config/package.js';
import {
	AUTH
} from '@/config/router.js';
import {
	getStorage
} from '@/utils/storage.js';
import Login from '../pages/auth/auth';
import MineCreation from '@/components/MineCreation';
// 微信登录
import {
	APP_ID
} from '@/config/common.js'
import {
	request
} from '@/config/http.js'
import {
	login
} from '@/utils/openLogin.js'
import {
	setStorage
} from '@/utils/storage.js'
import {
	LOGIN_WECHAT_LOGIN,
	LOGIN_WECHAT_GET_USERINFO,
	LOGIN_APP_REGISTER_LOGIN
} from '@/config/api.js'
import {
	AUTH_GXG_LOGO,
	AUTH_ENTER_IMG,
	AUTH_BTN_IMG
} from '@/config/image.js'
const regeneratorRuntime = require('@/utils/regenerator-runtime/runtime.js')

export default {
	props: {
		isLogin: {
			type: Boolean,
			default: false
		},
		userInfo: {
			type: Object,
			default: () => {
				return {};
			}
		},
		session_key: {
			type: String,
			default: ''
		},
		getStorageData: {
			type: Boolean,
			default: false
		}
	},
	components: {
	      Login,
		  // RegisteredPhone
	    },
	data() {
		return {
			loginbg: LOGIN_BG,
			isShowModel: false,
			codeValue: '获取验证码',
			avatar: AVATAR,
			phoneNum: '',
			value: '注册会员',
			codeNum: '',
			logonVisible:true,
			// 微信登录
			gxgLogo: AUTH_GXG_LOGO,
			gxgEnter: AUTH_ENTER_IMG,
			gxgBtn: AUTH_BTN_IMG
			
			
		};
	},
	watch: {
		userInfo(userInfo) {
			this.avatar = userInfo.avatarUrl;
			this.phoneNum = userInfo.phone;
			this.value = userInfo.phone ? userInfo.nickName : '注册会员';
			this.logonVisible == false
		}
	},
	methods: {
		showLoginModel: function() {
			this.isShowModel = !this.isShowModel
		},
		
		//获取验证码
		getCode: function() {
			const _this = this;
			if (this.codeValue == '获取验证码' && /^1\d{0,10}$/g.test(this.phoneNum)) {
				sendVerificationCode(this.phoneNum).then(res => {
					this.getCodeTime();
				});
			} else {
				wx.showToast({
					title: '请填写手机号码',
					icon: 'none',
					duration: 2000
				})
			}
		},
		
		//验证码倒计时
		getCodeTime: function (options) {
		  var that = this;
		  var currentTime = 90;
		  that.codeValue = currentTime + '秒'
		  let interval = setInterval(function () {
		    that.codeValue = (currentTime - 1) + '秒'
		    currentTime--;
		    if (currentTime <= 0) {
		      clearInterval(interval)
			  that.codeValue = '获取验证码'
		    }
		  }, 1000)
		},
		
		getPhoneNumber(e) {
			let iv = e.detail.iv;
			let encryptedData = e.detail.encryptedData;
		},
		
		getCardInfro() {
			bindPhone({
				phone: this.phoneNum,
				code: this.codeNum,
				name: this.userInfo.nickName
			}).then(res => {
				this.value = this.userInfo.nickName;
				this.showLoginModel();
			})
		},
		// 微信登录
		getUserInfo: async function(e) {
				if (!this.session_key) {
					return wx.showToast({
						title: '登录失败，重新授权试试',
						icon: 'none'
					})
				}
				const {
					encryptedData,
					iv
				} = e.detail
		
				//获取用户的微信信息
				const {
					openId,
					avatarUrl,
					nickName
				} = await request({
					method: 'POST',
					url: LOGIN_WECHAT_GET_USERINFO,
					data: {
						encryptedData,
						iv,
						"appId": APP_ID,
						"session_key": this.session_key
					},
					needToken: false,
					showLoadind: false,
					hideLoading: false,
					errorText: '登录失败'
				});
				//进行用户注册或登录，将返回的信息储存在本地缓存 
				const {
					header,
					data
				} = await request({
					method: 'POST',
					url: LOGIN_APP_REGISTER_LOGIN,
					data: {
						nickName,
						appId: APP_ID,
						openid: openId,
						headImg: avatarUrl
					},
					needToken: false,
					loadingText: '正在登录',
					returnHeader: true,
					errorText: '登录失败'
				});
				setStorage('sessionKey', this.session_key)
				setStorage('tempToken', data.token)
				setStorage('refreshToken', header.Authorization)
				setStorage('userInfo', data.UserInfo)
				setStorage('isLogin', true)
				let pages = getCurrentPages();
				let page = pages[pages.length - 1];
				page.onLoad()
				page.onShow()
				// let pages = getCurrentPages();
				// (pages.length === 0 || pages[pages.length - 1].route !== AUTH) && uni.reLaunch({ url: pages[pages.length - 1].route});
			}
		
	}
}
